<template>
<div>
    <row type="flex" justify="center" align="middle">
      <histogram-base> </histogram-base>
    </Row>
    <div>
      <Divider orientation="left" style="font-size: 0.5em;font-style: italic;">bug</Divider>
    </div>
    <div style="margin-bottom: 10px;">
      <Table
        :columns="columns"
        :data="bugList"
        style="width: 100%;"
      >
      </Table>
    </div>
  </div>
</template>
<script>
import HistogramBase from './HistogramBase.vue';
import { isEmpty } from "../util/help.js";
export default {
  props: ["data", "scenes"],
  data: function () {
    return {
      columns: [
        {
          title: '',
          key: 'title',
          align: 'center',
          width: '350'
        },
        {
          title: '',
          key: 'level',
          align: 'center'
        },
        {
          title: '',
          key: 'rd_owner',
          align: 'center'
        },
        {
          title: 'QA',
          key: 'qa_owner',
          align: 'center'
        },
        {
          title: '',
          key: 'create_time',
          align: 'center',
          width: '150'
        },
        {
          title: '',
          key: 'status',
          align: 'center',
          filters: [
            {
              label: '',
              value: ''
            },
            {
              label: '',
              value: ''
            },
            {
              label: '',
              value: ''
            },
            {
              label: '',
              value: ''
            }
          ],
          filterMultiple: false,
          filterMethod (value, row) {
            return row.status == value;
          },
          render: (h, params) => {
            const { status } = params.row;
            return h('div', [
                h('Tag', {
                props: {
                    color: this.setColor(status)
                }
                }, status)
            ]);
          }
        },
        {
          title: '',
          key: 'url',
          render: (h, params) => {
            return h('div', [h('a', {
              attrs: {
                href: params.row.url
              }
            }, isEmpty(params.row.url) == false ? '' : '-')])
          }
        }
      ],
      bugList: []
    }
  },
  components: {
    HistogramBase
  },
  mounted: function () {
    this.getData();
  },
  methods: {
    setColor(status) {
      switch (status) {
        case '':
          return 'green';
        case '':
          return 'blue';
        case '':
          return 'yellow';
        default:
          return 'red';
      }
    },
    async getData(commit, branch) {
      // icafe
      this.bugList = [
        {
          'title': 'Paddle Inferencewin cuda11.0', // 
          'level': 'P1',
          'rd_owner': '',
          'qa_owner': '',
          'create_time': '2022-02-22 10:05',
          'url': 'https://console.cloud.baidu-int.com/devops/icafe/issue/DLTP-45162/show',
          'status': ''
        },
        {
          'title': 'Paddle Inferencewin cuda11.0', // 
          'level': 'P3',
          'rd_owner': '',
          'qa_owner': '',
          'create_time': '2022-02-14 18:46',
          'url': 'https://console.cloud.baidu-int.com/devops/icafe/issue/DLTP-43997/show',
          'status': ''
        },
        {
          'title': 'pr:39573 PaddleNLP faster_ernie python ', // 
          'level': 'P1',
          'rd_owner': '',
          'qa_owner': '',
          'create_time': '2022-02-21 17:10',
          'url': 'https://console.cloud.baidu-int.com/devops/icafe/issue/DLTP-44863/show',
          'status': ''
        }
      ]
    }
  }
};
</script>

<style scoped>
.video-header {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 6px;
}
.container-content {
  overflow-y: auto;
}
</style>



